/* //////////////////// Utilities //////////////////// */

.fullWidth {
  width: 100%;
}

.divider {
  width: 100%;
  border-bottom: 1px solid $colorOutline;
}

.dash_divider {
  width: 100%;
  border-bottom: 1px dashed $colorOutline;
}

.srOnly {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: none;
  overflow: hidden;
  opacity: 0;
  z-index: -1;
}

.unstyledButton {
  display: inline-block;
  border: none;
  padding: 0;
  margin: 0;
  text-decoration: none;
  background: transparent;
  color: $colorText;
  -webkit-appearance: none;
  -moz-appearance: none;
  transition: box-shadow 200ms;

  &:focus {
    outline: none;
    box-shadow: 0 0 0 3px $colorTextLight;
  }
}

@mixin unstyledButton {
  display: inline-block;
  border: none;
  padding: 0;
  margin: 0;
  font-family: Montserrat, sans-serif;
  text-decoration: none;
  background: transparent;
  color: $colorText;
  -webkit-appearance: none;
  -moz-appearance: none;
  transition: box-shadow 200ms;

  &:focus {
    outline: none;
    box-shadow: 0 0 0 3px $colorTextLight;
  }
}

.greyBkg {
  min-height: calc(100vh - (6rem + 48px));
  background-color: $colorBkg;
}

.vertical_divider {
  border-left: 1px solid $colorOutile_diveder;
  height: 20px;
}

/* //////////////////// Custom scrollbar //////////////////// */

$scrollbarSize: 0.5rem;
$scrollbarMinLength: 1.5rem;
$scrollbarFirefoxWidth: thin;
$scrollbarTrackColor: rgba(0, 0, 0, 0.05);
$scrollbarColor: rgba(0, 0, 0, 0.3);
$scrollbarColorHover: rgba(0, 0, 0, 0.5);
$scrollbarColorActive: rgb(0, 0, 0);

.elementScrollX {
  overscroll-behavior: contain;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  scrollbar-width: $scrollbarFirefoxWidth;
}
.elementScrollY {
  overscroll-behavior: contain;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  scrollbar-width: $scrollbarFirefoxWidth;
}

.customScrollbar {
  scrollbar-color: $scrollbarColor $scrollbarTrackColor;

  &::-webkit-scrollbar {
    height: $scrollbarSize;
    width: $scrollbarSize;
  }
  &::-webkit-scrollbar-track {
    background-color: $scrollbarTrackColor;
  }
  &::-webkit-scrollbar-thumb {
    background-color: $scrollbarColor;
    border-radius: 1000px;
  }
  &::-webkit-scrollbar-thumb:hover {
    background-color: $scrollbarColorHover;
  }
  &::-webkit-scrollbar-thumb:active {
    background-color: $scrollbarColorActive;
  }
  &::-webkit-scrollbar-thumb:vertical {
    min-height: $scrollbarMinLength;
  }
  &::-webkit-scrollbar-thumb:horizontal {
    min-width: $scrollbarMinLength;
  }
}

/* //////////////////// General //////////////////// */

*,
::after,
::before {
  box-sizing: border-box;
}

html {
  padding-bottom: 0;
}

body {
  font-family: Montserrat, sans-serif;
  color: $colorText;
}

/* //////////////////// Text //////////////////// */

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: bold;
  line-height: 1.15;
}

.base_text {
  font-size: 1rem;
  color: $colorText;
}

.base_title {
