/* //////////////////// Button //////////////////// */
/* TODO : Redo as component in ui-components */

/* ////////// Base ////////// */

.button {
  padding: 0.5625rem 2rem;
  border: 1px solid $colorMain;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: normal;
  color: $colorTextInverse;
  background-color: $colorMain;
  box-shadow: none;
  outline: none;
  transition: border 200ms, color 200ms, background-color 200ms, box-shadow 200ms;
  &:disabled {
    cursor: not-allowed;
    opacity: 0.5;
  }
  &:not(:disabled) {
    cursor: pointer;
  }
  &:hover {
    border-color: $colorMain_light;
    background-color: $colorMain_light;
  }
  &:active {
    border-color: $colorMain_dark;
    background-color: $colorMain_dark;
  }
  &:focus {
    box-shadow: 0 0 0 3px $colorMain_shadow;
    outline: none;
  }
}

.buttonIcon_right {
  margin-right: 0.375rem;
}

.button_disable {
  background-color: lightgrey;
  border-color: lightgrey;
  color: gray;
  &:hover {
    background-color: lightgrey;
    border-color: lightgrey;
    color: gray;
  }
  &:active {
    background-color: lightgrey;
    border-color: lightgrey;
    color: gray;
  }
}

.back_btn {
  text-decoration: underline;
  cursor: pointer;
  color: $colorTextDarkGrey;
  &:hover {
    color: $colorMainBlue_cta;
  }
}

/* ////////// Color ////////// */

.buttonColor_alt {
  border-color: $colorMainAlt;
  background-color: $colorMainAlt;

  &:hover {
    border-color: $colorMainAlt_light;
    background-color: $colorMainAlt_light;
  }
  &:active {
    border-color: $colorMainAlt_dark;
    background-color: $colorMainAlt_dark;
  }
  &:focus {
    box-shadow: 0 0 0 3px $colorMainAlt_shadow;
  }
}

.buttonColor_success {
  border-color: $colorSuccess;
  background-color: $colorSuccess;

  &:hover {
    border-color: $colorSuccess_light;
    background-color: $colorSuccess_light;
  }
  &:active {
    border-color: $colorSuccess_dark;
    background-color: $colorSuccess_dark;
  }
  &:focus {
    box-shadow: 0 0 0 3px $colorSuccess_shadow;
  }
}

.buttonColor_warning {
  border-color: $colorWarning;
  color: $colorText;
  background-color: $colorWarning;

  &:hover {
    border-color: $colorWarning_light;
    background-color: $colorWarning_light;
  }
  &:active {
    border-color: $colorWarning_dark;
    background-color: $colorWarning_dark;
  }
  &:focus {
    box-shadow: 0 0 0 3px $colorWarning_shadow;
  }
}

.buttonColor_danger {
  border-color: $colorDanger;
  background-color: $colorDanger;

  &:hover {
    border-color: $colorDanger_light;
    background-color: $colorDanger_light;
  }
  &:active {
    border-color: $colorDanger_dark;
    background-color: $colorDanger_dark;
  }
  &:focus {
    box-shadow: 0 0 0 3px $colorDanger_shadow;
  }
}

/* ////////// Block ////////// */

.button_block {
  width: 100%;
}

/* ////////// Big ////////// */

.button_big {
  padding: 0.75rem 2rem;
  font-size: 1.125rem;
  font-weight: bold;
}

/* ////////// Big ////////// */

.button_big_icon {
  padding: 0.75rem 1rem;
  font-size: 1rem;
  font-weight: 400;
}

/* ////////// Small ////////// */

.button_small {
  font-size: 0.875rem;
}

/* ////////// Button Link ////////// */

button.button.button_link {
  background: none;
  border: none;
  color: #476e9f;
  font-size: 0.8rem;
  font-weight: 500;
  padding: 0;
  margin: -15px 0 20px 0;
}

/* ////////// Outline ////////// */

.button_outline {
  border-color: $colorOutline;
  color: $colorTextLight;
  background-color: transparent;

  &:hover {
    border-color: $colorOutline;
    color: $colorTextLight;
    background-color: $colorBkg;
  }
  &:active {
    border-color: $colorOutline;
    color: $colorText;
    background-color: transparent;
  }
  &:focus {
    box-shadow: 0 0 0 3px $colorTextLight_shadow;
  }
}

/* ////////// Clear ////////// */

.button_clear {
  padding: 0.25rem 0.375rem;
  font-weight: bold;
  color: $colorMain;
  background-color: transparent;

  &:hover {
    color: $colorMain_light;
    background-color: transparent;
  }
  &:active {
    color: $colorMain_dark;
    background-color: transparent;
  }
}

/* ////////// Shadow ////////// */

.button_shadow {
  box-shadow: 0 3px 16px rgba(0, 0, 0, 0.15);
}

/* ////////// Round ////////// */

.button_round {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2rem;
  height: 2rem;
  margin: 0 1px;
  padding: 0;
  font-size: 1.125rem;
  border: none;
  border-radius: 50%;
  text-align: center;
  color: $colorText;
  background-color: transparent;
  white-space: nowrap;
}
.button_round:hover {
  background-color: $colorBkg;
}
.button_round:focus {
  box-shadow: 0 0 0 3px $colorGrey_shadow;
  outline: none;
}

/* ////////// Full width ////////// */

.button_full {
  width: 100%;
}

@media screen and (max-width: $mediaqueryS) {
  .button_full_s {
    width: 100%;
  }
}

/* //////////////////// Alert //////////////////// */
/* TODO : Redo as component in ui-components */

.alert {
  display: flex;
  padding: 0.75rem;
  font-size: 0.875rem;
  border-radius: 6px;
}

.alert_danger {
  background-color: $colorDanger_altBkg;
  color: $colorDanger_altText;
}

.alert.alert-success.ng-star-inserted {
  background: #dbffc7;
  padding: 0.3rem 1rem;
  margin: 0 0 12px 0;
}

.alert__icon {
  margin-right: 0.75rem;
  font-size: 1rem;
}

.alert__title,
.alert__text {
  margin: 0;
  font-size: 0.875rem;
}

.alert_succes_color {
  color: $colorSuccess_altText;
}

/* //////////////////// Card //////////////////// */

.card {
  border-radius: 8px;
  background-color: white;
  box-shadow: 0 3px 16px rgba(0, 0, 0, 0.1);
}

.card_border {
  border-radius: 8px;
  background-color: white;
  border: 1px solid $colorOutline;
}

.authCard {
  // width: calc(100% - 3rem);
  margin: 1.5rem;
  .card_small {
    max-width: 360px;
    min-width: 360px;
  }
  .card_large {
    max-width: 900px;
  }
}

.cardHead {
  margin: 0;
  padding: 1.5rem;
  border-bottom: 1px solid $colorOutline;
  font-size: 24px;
  color: $colorMainBlue;
}

.authCardLogo {
  display: block;
  max-width: 100%;
  width: 160px;
}

.cardBody {
  padding: 1.5rem;
}

.cardTitle {
  margin: 0 0 1.5rem 0;
  font-size: 1.5rem;
}

.cardSubtitle {
  margin: 0 0 1.5rem 0;
  font-size: 1.5rem;
  color: $colorMainBlue;
  font-size: 20px;
}

.cardTitle.cardTitle_small {
  font-size: 0.875rem;
}

/* //////////////////// Input //////////////////// */

/* ////////// Container ////////// */

.inputCont {
  margin-bottom: 1.5rem;
}
.inputCont_form {
  display: flex;
  align-items: flex-start;
}
@media screen and (max-width: $mediaqueryS) {
  .inputCont_form {
    flex-direction: column;
  }
}

/* ////////// Label ////////// */

.label {
  display: inline-block;
  margin-bottom: 0.25rem;
  font-size: 0.875rem;
  color: $colorTextLight;
  &.required_icon:after {
    content: ' *';
    color: red;
  }
}
.inputCont_form .label {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 25%;
  min-height: 2.25rem;
  margin: 0 1rem 0 0;
  font-weight: bold;
  color: $colorText;
}
@media screen and (max-width: $mediaqueryS) {
  .inputCont_form .label {
    justify-content: flex-start;
    width: 100%;
    min-height: 0;
    margin-bottom: 0.5rem;
  }
}

/* ////////// Icon ////////// */

.inputIconCont {
  position: relative;
}
.inputIconField {
  padding-right: 2.5rem;
}
.inputIconBtn {
  position: absolute;
  top: 50%;
  right: 0.625rem;
  transform: translateY(-50%);
  width: 1.5rem;
  height: 1.5rem;
  padding: 0.125rem;
  border: none;
  border-radius: 1000px;
  font-size: 1rem;
  color: $colorTextLight;
  background-color: transparent;
  transition: box-shadow 200ms;
}
.inputIconBtn:focus {
  box-shadow: 0 0 0 3px $colorOutline;
  outline: none;
}

/* ////////// Checkbox ////////// */

.checkboxCont {
  display: flex;
  align-items: center;
}
.checkboxCont_large {
  align-items: flex-start;
}

.checkboxLabel {
  font-size: 0.875rem;
  margin: 0 0 0 0.5rem;
  padding: 0.25rem 0;
  line-height: 1;
}
.checkboxCont_large .checkboxLabel {
  padding: 0;
}

.checkboxLabel__title {
  display: block;
  padding-bottom: 0.5rem;
  font-size: 1rem;
  color: $colorText;

  i {
    margin-right: 0.25rem;
  }
}
.checkboxLabel__subtitle {
  color: $colorTextLight;
}

/* ////////// Form ////////// */

.inputCont__end {
  display: flex;
  width: 75%;
  min-height: 2.25rem;
}
@media screen and (max-width: $mediaqueryS) {
  .inputCont__end {
    width: 100%;
    min-height: 0;
  }
}

/* //////////////////// Containers //////////////////// */

.wrapperMainCont {
  max-width: 80%;
  margin: 0 auto;
  margin-top: 150px;
}

.wrapperCont {
  margin: 0 auto;
  max-width: 80%;
}

.privateBaseCont {
  position: relative;
  min-height: calc(100vh - 48px);
  margin-top: 48px;
}

.bkgCont {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 100vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.fixedAside {
  position: sticky;
  top: calc(48px + 7.5rem);
  z-index: 900;
}
@media screen and (max-width: $mediaqueryS) {
  .fixedAside {
    position: static;
  }
}

/* //////////////////// Dropdown //////////////////// */

.p-overlaypanel.dropdown {
  min-width: 200px;
  margin-top: 0;
  border-radius: 8px;
  text-align: left;
  z-index: 800 !important;

  &::before,
  &::after {
    display: none;
  }

  .p-overlaypanel-content {
    padding: 0;
  }
}

.dropdownList {
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-family: Montserrat, sans-serif;
}
.dropdownList_margin {
  margin: 0.5rem 0;
}

.dropdownList__title {
  display: block;
  font-size: 0.75rem;
  font-weight: bold;
  padding: 0.75rem 1rem 0.25rem 1rem;
  color: $colorHeaderTextLight;
}

.dropdownList__btn {
  @include unstyledButton;
  position: relative;
  display: block;
  width: 100%;
  padding: 0.75rem 1rem;
  font-size: 0.875rem;
  text-align: left;
  transition: background-color 200ms, box-shadow 200ms;

  &:hover {
    background-color: $colorHeaderHover;
  }
  &:focus {
    box-shadow: 0 0 0 2px $colorHeaderTextLight;
  }

  .headerNavBadge {
    top: 50%;
    right: 0.75rem;
    transform: translateY(-50%);
  }
}
.dropdownList__btn_bold {
  font-weight: bold;
}
.dropdownList__btn_borderTop {
  border-top: 1px solid $colorHeaderOutline;
}
.dropdownList__btn_last {
  border-radius: 0 0 8px 8px;
}

.dropdownList__btn__icon {
  width: 1rem;
  margin-right: 0.75rem;
}

/* //////////////////// History //////////////////// */

.toggleHistoryBtn {
  position: fixed;
  display: none;
  align-items: center;
  justify-content: center;
  bottom: 1.5rem;
  right: 1.5rem;
  width: 3rem;
  height: 3rem;
  font-size: 1.5rem;
  border-radius: 1000px;
  box-shadow: 0 3px 16px rgba(0, 0, 0, 0.1);
}
.closeHistoryBtn {
  display: none;
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
}

/* //////////////////// Separator //////////////////// */
.separator {
  display: flex;
  align-items: center;
  margin: 14px 0;
  font-size: 14px;
  color: $colorTextLight;
  text-align: center;
}

.separator::before,
.separator::after {
  content: '';
  flex: 1;
  border-bottom: 1px solid $colorMain_shadow;
}

.separator:not(:empty)::before {
  margin-right: 0.25em;
}

.separator:not(:empty)::after {
  margin-left: 0.25em;
}

@media screen and (max-width: $mediaqueryS) {
  .historyCard {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 0;
    z-index: 1200;
    transform: translateX(100%);
    transition: transform 300ms;
  }
  .historyCard_visible {
    transform: translateX(0);
  }

  .toggleHistoryBtn {
    display: flex;
  }
  .closeHistoryBtn {
    display: block;
  }
}
